@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
* {
  font-family: 'Comfortaa', cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.header-h1 {
    margin: 0;
    padding: 2rem 0 1rem 0;
    text-align: center;
    letter-spacing: 0.5px;
    font-weight: 500;
    color: rgb(0, 0, 0);
}

.header-p {
    margin: 0;
    padding: 1rem;
    text-align: center;
    font-size: small;
    color: rgb(100, 100, 100);
}
.data {
    padding: .5rem;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1rem;
    border-radius: 5px;
    background-color: rgb(245, 245, 225);
    cursor: pointer;
}

.question {
    position: relative;
    font-size: x-large;
    padding: 1rem;
    transition: all 0.3s ease;
}

.question::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 13px;
    height: 13px;
    background-image: url(/pyt-faq-page/static/media/arrow-down.5b0512cf.svg);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: all 0.3s ease;
}

.answer {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease;
}

.data.expanded .question {
    margin-bottom: 15px;
}

.data.expanded .question::after {
    -webkit-transform: translateY(-50%) rotate(180deg);
            transform: translateY(-50%) rotate(180deg);
}

.data.expanded .answer {
    max-height: 1000px;
    opacity: 1;
}
.data-container {
    width: 100%;
    margin: auto;
    padding: 1rem;
}

